.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control{
    width: 50%;
    display: flex;
    align-items: center;
    input{
        width: 100%;
        
        -webkit-appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem;
    }
}
.play-control{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    width: 30%;
    svg{
        cursor: pointer;
    }
}
input[type="range"]:focus{
    outline:none;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height:16px;
    width:16px;
    
}
input[type="range"]::-moz-range-thumb{
    -webkit-appearance: none;
    background: transparent;
    border: none;
    
}

.track{
    background:lightblue;
    width:100%;
    height:1rem;
    border-radius: 10px;
    position:relative;
    overflow: hidden;
}
.animation-track{
    background:rgb(205, 205, 205);
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
   transform: translateX(0%);
    pointer-events: none;
}

@media screen and (max-width:768px){
    .time-control{
        width: 90%;
    }
    .play-control{
        width:60%
    }
}